import React, { useState, useEffect } from 'react';

import Section from '../Section/Section';
import Auth from '../../components/Admin/Auth/Auth';
import Inbox from '../../components/Admin/Inbox/Inbox';

import classes from './Admin.module.css';

const Admin = ({ closePanel }) => {
    const [logged, setLogged] = useState(false);

    useEffect(() => {
        checkLogin();
    }, []);

    const closeModal = (element) => {
        if(element === document.getElementsByClassName(classes.Modal)[0]) {
            document.body.style.overflow = 'auto';
            closePanel();
        }
    }

    const checkLogin = () => {
        let token = localStorage.getItem('token');
        let expirationDate = localStorage.getItem('expirationDate');

        if(!token || !expirationDate) {
            return setLogged(false);
        }

        if(expirationDate < new Date()) {
            return setLogged(false);
        }

        if(!logged) {
            return setLogged(true);
        }
    }

    return (
    	<div className = { classes.Admin }>
    		<div className = { classes.Modal } onClick = { (event) => closeModal(event.target) } />
            <div className = { classes.Console }>
                <Section meta = { logged ? 'Inbox' : 'Login' } name = { logged ? 'Messages' : 'Admin page' } description = { logged ? 'Incoming messages' : 'This is a restricted area. Please, enter your email and password below.' } />
                { logged ? <Inbox /> : <Auth success = { () => setLogged(true) } /> }
            </div>
    	</div>
    );
}

export default Admin;
