const getAllTypes = () => {
    let allTypes = [];

    for(let category of Object.keys(services)) {
        for(let type of services[category].types) {
            allTypes.push(type);
        }
    }
    
    return allTypes;
}

const Services = () => {
    const [activeCategory, setActiveCategory] = useState('All');
    const [activeType, setActiveType] = useState(window.screen.width >= 720 ? 0 : null);
    const [activeService, setActiveService] = useState(null);
    
    const switchCategory = (category) => {
        setActiveType(window.screen.width >= 720 ? 0 : null);
        setActiveService(null);
        setActiveCategory(category);
    }

    const switchType = (type) => {
        if(activeType === type && window.screen.width < 720) {
            setActiveType(null);
        } else {
            setActiveService(null);
            setActiveType(type);
        }
    }

    const switchService = (service) => {
        if(activeService === service) {
            setActiveService(null)
        } else {
            setActiveService(service);
        }
    }
    
    const selectedServices = activeType != null && <div className = { classes.Services }>
            { (activeCategory === 'All' ? getAllTypes()[activeType].services : services[activeCategory].types[activeType].services).map((service, i) => (
                <div className = { [classes.Service, activeService === i ? classes.ActiveService : ''].join(' ') } onClick = { () => switchService(i) } key = { service.name }>
                    <div className = { classes.Line }>
                        <p className = { classes.Name }>{ service.name }</p>
                        <p className = { classes.Price }>now £{ ('' + service.price).includes('.') ? service.price.toFixed(2) : service.price }</p>
                    </div>
                    
                    <div className = { classes.Line }>
                        <p className = { classes.Time }>{ service.time.split(':')[0] !== '0' && service.time.split(':')[0] + ' hrs' } { service.time.split(':')[1] !== '00' && service.time.split(':')[1] + ' mins' }</p>
                        <p className = { classes.Discount }>save up to 20%</p>
                    </div>
                </div>
            )) }

            <a className = { [classes.Book, activeService != null ? classes.ActiveBook : ''].join(' ') } href = { 'https://www.treatwell.co.uk/place/beauty-at-luxx-hair/' } target = '_blank' rel = 'noopener noreferrer'>Book now</a>
        </div>
    
    return (
    	<div className = { classes.Services }>
    		<div className = { classes.Categories }>
                <div className = { [classes.Category, activeCategory === 'All' ? classes.Active : ''].join(' ') } onClick = { () => switchCategory('All') }>
                    <img src = { all } alt = 'All' />
                    <p>All</p>
                </div>

                { Object.keys(services).map(category => (
                    <div className = { [classes.Category, activeCategory === category ? classes.Active : ''].join(' ') } onClick = { () => switchCategory(category) } key = { category }>
                        <img src = { services[category].icon } alt = { category } />
                        <p>{ category }</p>
                    </div>
                )) }
            </div>

            <div className = { classes.Types }>
                { (activeCategory === 'All' ? getAllTypes(activeType, switchType) : services[activeCategory].types).map((type, i) => (
                        <React.Fragment key = { type.name }>
                            <div className = { [classes.Type, activeType === i ? classes.Active : ''].join(' ') } onClick = { () => switchType(i) }>
                                <p className = { classes.Name }>{ type.name }</p>
                                <img className = { [classes.Expand, activeType === i ? classes.ExpandOpen : ''].join(' ') } src = { expand } alt = 'Expand' />
                            </div>          
                            
                            { activeType === i && window.screen.width < 720 ? selectedServices : null }
                        </React.Fragment>
                    ))
                }
            </div>

            { window.screen.width >= 720 && selectedServices }
    	</div>
    );
}

export default Services;
